import Vue from 'vue'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
 
// 
const messages = {
  'zh': require('./zh.js'),   // 
  'en': require('./en.js')    // 
}
 
console.log(messages)
//  export default
export default new VueI18n({
  locale : 'en', // set locale 
  messages : messages // set locale messages
})


